    .dim_body{
        &:after{
            z-index: 5;
            position: fixed;
            top:0;
            left:0;
            width:100vw;
            height: 100vh;
            opacity:1;
            background: #3a3a3a;
            content: '';
        }
    }
    input {
        -webkit-border-radius: 0;
        -moz-border-radius: 0;
        border-radius: 0;
    }
    textarea{
        margin-top: 10px !important;
        font-size: 11px !important;
    }
    .custom-input{
        width:auto;
        margin: 15px 0 0 0 !important;
        >.title.addonbtn{
            margin: 0 !important;
            font-size: 11px !important;
            text-align: left !important;
        }
    }
    .title-tultip{
        max-width: calc(100% - 70px);
        padding:23px 15px;
    }
    .wrap-inner{
        max-width: calc(100vw - 110px);
        display: inline-block;
        white-space: nowrap;
        & + button{
            position: absolute;
            right: 15px;
            top: 21px;
            .trash_box.icon-edit{
                border:none;
            }
        }

        .text1{
            display: inline-block;
            width: 90px;
            vertical-align: bottom;

        }
        .text2{
            width: calc(100% - 100px);
            display: inline-block;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            vertical-align: bottom;
        }
    }
    .dropmenu {
        &.type3 {
            &.innerTable{
                margin-right: 0;
            }
        }
    }
