    .bx-wrapper {
        .bxslider {

            li .text {
                max-width: 80%;
                left: 30px;
                max-height:300px;
                height: 180px;

                p:first-of-type {
                    font-size: 30px !important;
                    margin-top: 20px;
                    margin-bottom: 0;
                }
                p{
                    color: #333 !important;
                    font-size: 11px !important;
                    span{
                        display: inline-block;
                        padding-top: 3px !important;
                        font-size: 11px !important;

                    }
                }
                p:last-of-type {
                    font-size: 11px !important;
                    margin-top: 0;
                }
            }
        }
