            width: 98px;
            height: 0;
            // top: 25px;
            //bottom: 23%;
            max-height: 300px;
            // left: auto;
            // right: 18%;
            .bx-prev {
                left: 3px;
            }
            .bx-next {
                right: 3px;
            }
            .bx-prev:before,
                .bx-next:before {
                width: 15px;
                height: 15px;
            }
            .bx-pager {
                // left: 0;
                bottom: 7px;

            }
            .bx-controls-direction {
                right: 0;
            }
        }
