        .login-tap {
            > .middle {
                .guestbtn > p {
                    font-size: 12px;
                }
                > .wrap {
                    margin: 11px 0;
                }
                > .block-white-type-button {
                    margin-top: 19px;
                    & + .block-white-type-button {
                        margin-top: 5px;
                    }
                }
                > .underline_calm-type-button {
                    margin-top: 13px;
                }
            }
        }

        .login-tap:not(.not-index) {
            overflow: visible;
            .close-btn {
                display: none;
            }
            height: initial;
            height: auto;
            position: static !important;
            //                width: 100%;
            height: 100%;
            //        display: flex;
            //          $m_marginbottom: 15px + $m_margintop;
            > .top {
            }
            // $margin_:30px;
            > .middle {
                top: calc((100% + #{$logo-height}) / 2);
                height: calc((100% - 39px) / 2);
                > * {
                    position: absolute;
                    width: initial;
                    width: auto;
                    left: 23px;
                    right: 23px;
                    margin: 0;
                }
                > *:nth-child(1) {
                    top: 7%;
                }
                > *:nth-child(2) {
                    top: 18%;
                }
                > *:nth-child(3) {
                    top: 33%;
                    margin: 0;
                }
                > *:nth-child(4) {
                    top: 54.23%;
                    width: calc(100% - 46px);
                }
                > *:nth-child(5) {
                    top: 66.9%;
                    width: calc(100% - 46px);
                }
                > *:nth-child(6) {
                    top: 85.8%;
                    width: calc(100% - 46px);
                }
                .signbtn {
                    top: 8%;
                }
                .guestbtn {
                    &:before {
                        display: none;
                    }
                    top: 70%;
                }
            }
            > .bottom {
                display: block;
                top: calc(100% + #{$index-right-bottom-height - $margin_});
                bottom: initial;
                bottom: auto;
                background-color: $positive;
                .wrap {
                }
                &.ownerType {
                    top: calc(100% + 169px);

                }
