    //171130 entry mobile
    .entry_main{
       article{
           overflow: hidden;
       }
    }
    .m_wrap{
        height: 100%;

        .m_entry_content{
            height:calc(100% - 40px);
            >div{
                overflow: hidden;
                position: relative;
                height: 100%;
            }
            .m_img{
                width: 100%;
                position: absolute;
                top: 20px;
                display: inline-block;
                background: url("../../img/m_entry.png") no-repeat top;
                width: 100%;
                height: 256px;
                background-size:cover;
                background-position:50% 50%;

                @media all and (min-width: 500px) and (max-width: 1279px) {
                    height:514px;
                }

            }

            .m_link{
                padding-top: 360px;
                text-align: center;
                @media all and (min-width: 360px) and (max-width: 499px) {
                    padding-top: 390px;
                }
                @media all and (min-width: 500px) and (max-width: 1279px) {
                    padding-top: 620px;
                }

                a{
                    display: inline-block;
                    padding-top: 64px;
                    text-align: center;
                    color: #777;
                    font-size: 14px;
                    font-weight: 500;
                    vertical-align: middle;
                }
            }

            .m_link_owner{
                background: url("../../img/m_owner.png") no-repeat top;
                margin-right: 7%;
                background-size:70px;
            }
            .m_link_installer{
                background: url("../../img/m_installer.png") no-repeat top;
                margin-left: 7%;
                background-size:70px;
            }

        }

    }

    .entry_main .m-bottom{
        height: 40px;
        width: 100%;
        display: inline-block;
        vertical-align: middle;
        position:fixed;
        bottom:0;
        cite{
            text-align: center;
            font-size: 9px;
            color: #4a5166;
            opacity: 1;
            display: inline-block;
            vertical-align: middle;
            width: 97%;
        }


        &:after{
            content:'';
            vertical-align: middle;
            display: inline-block;
            width: 0;
            height: 100%;
        }
    }
